@import "_vars";

@font-face {
	font-family: "Stolzl";
	src: url("#{$fonts}Stolzl-Bold.woff2") format("woff2"), url("#{$fonts}Stolzl-Bold.woff") format("woff");
	font-weight: bold;
	font-style: normal;
	font-display: swap;
}

